import React, { Component } from 'react';
import './App.css';
import CalculatorButton from "./CalculatorButton";

class App extends Component {
    constructor(){
        super();

        this.state = {
            prev_result: "",
            result: ""
        }
    }

    onClick = button => {

        if(button === "="){
            this.calculate()
        }

        else if(button === "C"){
            this.clear()
        }
        else if(button === "CE"){
            this.clearEntry()
        }

        else {
            this.setState({
                result: this.state.result + button
            })
        }
    };

    calculate = () => {
        const interim_result = this.state.result

        const final_expression = this.bracketFix(interim_result)
        try {
            this.setState({
                result: (eval(final_expression) || "" ) + "",
                prev_result: interim_result + "=" + eval(final_expression)
            })
        } catch (e) {
            this.setState({
                result: "",
                prev_result: "Error"
            })

        }
    };

    clear = () => {
        this.setState({
            result: ""
        })
    };

    clearEntry = () => {
        this.setState({
            result: this.state.result.slice(0, -1)
        })
    };

    bracketFix = (expression) => {
        return expression.replace(/([\d)])\(/, "$1*(")
    }


    render() {
        return (
                <div className="calculator-body">
                    <h1>SENG 513 Calculator</h1>
                    <div className="previousResult">
                        {this.state.prev_result}
                    </div>
                    <div className="result">
                        {this.state.result}
                    </div>
                    <CalculatorButton onClick={this.onClick}/>
                </div>
        );
    }
}

export default App;
